/* About.css */

.about {
  padding: 20px;
  position: relative; /* Allows positioning of top-left-text */
}

.top-left-text {
  position: absolute;
  top: 20px; /* Adjust top position as needed */
  left: 20px; /* Adjust left position as needed */
  font-size: 14px; /* Adjust font size as needed */
  line-height: 2.5; /* Adjust line height as needed */
  color: #101b11; /* Adjust text color as needed */
}

.gallery {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  gap: 0px;
}

.gallery-item {
  position: relative;
  flex: 1 1 calc(33.33% - 10px); /* Adjust the width as needed */
  /* overflow: hidden; */
}

.gallery-item img {
  width: 100%;
  position: relative;
  z-index: 1;
  margin: 20px -10px 0px 0px; /* Overlay each image over the next one */
}

gallery-item:last-child img {
  margin-left: 0; /* Remove margin from the last image */
}