/* NavigationBar.css */
.navigation-bar {
  display: flex;
  background-color: #f2fff2;
  padding: 10px 20px;
  margin-top: 20px;
  margin-bottom: 30px;
}

.nav-cell {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.nav-cell-left {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.nav-cell-right {
  flex: 1;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
}

.nav-cell-right a:hover {
  color: #f2fff2;
  background-color: #101b11;
}

.nav-cell-left a:hover {
  color: #f2fff2;
  background-color: #101b11;
}

.nav-cell-right a {
  text-decoration: none;
  font-family: 'Acumin Variable Concept', sans-serif;
  font-weight: 600;
  font-size: 18px;
  padding: 3px 5px;
  color: #101b11;
  transition: color 0.2s;
}

.nav-cell-left a {
  text-decoration: none;
  font-family: 'Acumin Variable Concept', sans-serif;
  font-weight: 600;
  font-size: 18px;
  padding: 3px 5px;
  margin: 0;
  color: #101b11;
  transition: color 0.2s;
}
