/* ProjectGrid.css */
.project-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    background-color: #f2fff2;
    padding: 10px;
  }

  .project-grid a {
    text-decoration: none;
    padding: 0%;
    margin: 0%;
  }
  
  .project-card {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    text-align: left;
    padding: 4px;
    background-color: #f2fff2;
  }
  
  .project-card img {
    width: 100%;
    object-fit: cover;
    height: auto;
    max-width: 100%;
    max-height: 100%; 


  }

  .project-image {
    width: 100%;
    max-width: 100%; 
    max-height: 100%; 
  }
  
  .project-card h3 {
    padding-top: 35px;
    opacity: 0;
    font-family: 'Acumin Variable Concept', sans-serif;
    font-size: 16px;
    font-weight: 600;
    color: #101b11;
  }
  
  .project-card:hover h3 {
    opacity: 1; 
  }