/* ProjectPage.css */
/* Define your CSS styles for the project page layout here */
.project-page {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 10px 20px;
  margin-top: 20px;
  margin-bottom: 30px;
  position: relative;
}

.project-item {
  flex: 1;
  display: flex;
  /* align-items: center; */
  justify-content: flex-start;
}

.project-image {
  flex: 1;
  padding: 0px;
  max-width: 100%;
}

.project-image img {
  max-width: 100%;
  height: auto;
}

.project-image h3 {
  font-family: 'Acumin Variable Concept', sans-serif;
  font-size: 18px;
  font-weight: 600;
  color: #101b11;
  margin-top: 40px;
}

.project-description {
  flex: 2;
  padding: 0px;
  display: flex;
  justify-content: flex-end;
  margin-top: 0%;
  
}

.project-info {
  flex: 1;
  padding: 0px 0px;
  position: sticky;
  top: 0px;
  width: 100%;
  display: flex;
  justify-content: flex-end;
  z-index: 1;
}

.fixed-info {
  background-color: #f2fff2;
  padding: 0px;
}

.justified-text {
  text-align: justify;
  width: 75%;
  margin-top: 0%;
}

.info-text {
  width: 50%;
  margin-top: 0%
}

.project-imgs {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  max-width: 78%;
  padding: 20px;
}

.project-imgs img {
  max-width: 83%;
  margin-top: 5px;
}